#curation-title {
	display: block;
	max-width: 100%;
	font-size: 5em;
	text-align: center;
}
#curation-year {
	font-size: 3.5em;
	text-align: center;
}
#curation-gallery {
	width: 60vw;
	min-width: 340px;
	height: 60vh;
	min-height: 250px;
	margin: 40px auto;
	background-color: transparent;

	.gallery-slide {
		width: 100%;
		height: 100%;
		object-fit: contain;
	}
}
p {
	font-size: 1.5em;
}